import http from '../axios';

export const RfPluginUpgradeService = pluginName => {
  const endpoint = `rf-plugin-upgrade/${pluginName}`;
  return {
    endpoint,
    fetch: () => {
      return http.get(endpoint);
    },
    download: plugins => {
      const url = endpoint + '/download';
      return http.post(url, plugins);
    },
    install: plugins => {
      const url = endpoint + '/install';
      return http.post(url, plugins);
    },
    uninstall: plugins => {
      const url = endpoint + '/uninstall';
      return http.post(url, plugins);
    },
    fetchDownloadState: () => {
      const url = endpoint + '/downloadState';
      return http.get(url);
    },
    deleteDownloadState: () => {
      const url = endpoint + '/downloadState';
      return http.delete(url);
    },
  };
};
