<template>
  <v-card>
    <v-card-title> <back-button></back-button> Plugin Distro </v-card-title>
    <v-card-text>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <generic-grid
            :key="remount"
            itemKey="name"
            :service="service"
            :creatable="false"
            :deletable="false"
            :hideActions="true"
            :headers="gridHeaders"
            :toolbarTitle="toolbarTitle"
          >
            <template v-slot:gridItemActions="{ item }">
              <rbx-switch
                :value="item.is_installed"
                @input="() => onGridItemInstallChange(item)"
                enableText="Installed"
                disableText="Not Installed"
              ></rbx-switch>
            </template>
            <template v-slot:toolbarButtons="{ selectedItems, clearSelectedItems }">
              <span class="mr-2">
                <v-btn
                  :disabled="isDeletingDownloadState"
                  @click="() => toggleDialog(dialogNames.multiStepDownloadInstall, selectedItems, clearSelectedItems)"
                  x-small
                  color="downloadColor"
                  class="btn-height-22 white--text"
                >
                  Download Apps
                  <v-icon x-small right dark> mdi-download </v-icon>
                </v-btn>
                <v-progress-circular
                  v-if="showDownloadProgressIcon"
                  :width="2"
                  :size="15"
                  color="white"
                  indeterminate
                ></v-progress-circular>
                <v-icon v-if="showCheckDownloadIcon" x-medium dark> mdi-check</v-icon>
              </span>
              <v-btn
                :disabled="selectedItems && selectedItems.length == 0"
                @click="() => toggleDialog(dialogNames.uninstall, selectedItems, clearSelectedItems)"
                x-small
                color="error"
                class="btn-height-22 white--text mr-2"
              >
                Uninstall Apps
                <v-icon x-small right dark> mdi-delete </v-icon>
              </v-btn>
            </template>
          </generic-grid>
        </v-col>
      </v-row>
    </v-card-text>
    <confirm-dialog
      :is-open="dialog[dialogNames.uninstall]"
      :on-close="() => closeDialog(dialogNames.uninstall)"
      :on-confirm="() => onUninstall()"
      :loading="isUninstalling"
      dialog-text="Do you want to uninstall these apps ?"
    ></confirm-dialog>

    <v-dialog
      v-model="dialog[dialogNames.multiStepDownloadInstall]"
      :persistent="isUpgradePersistent"
      max-width="500px"
    >
      <rf-plugin-download
        :selectedPlugins="selectedItem"
        :startDownload="downloadPlugin"
        :installPlugins="installPlugins"
        :appDownloadStates="appDownloadStates"
        :clearSelectedApps="clearSelectedApps"
        :currentDownloadState="currentDownloadState"
        :deleteDownloadState="deleteDownloadState"
        :clearDownloadStateInterval="clearDownloadStateInterval"
        v-if="remountDialog[dialogNames.multiStepDownloadInstall]"
        :closeDialog="(val, config) => closeDialog(dialogNames.multiStepDownloadInstall, config)"
      ></rf-plugin-download>
    </v-dialog>
  </v-card>
</template>
<script>
import RbxSwitch from '../../Common/RbxSwitch.vue';
import BackButton from '../../Common/BackButton.vue';
import RfPluginDownload from './rfPluginDownload.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import ConfirmDialog from '../../Common/ConfirmDialog.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { DOWNLOAD_STATES, REFRESH_TIME } from './rfPluginDownloadStates.js';
import rfFlowFrameworkUpgradeService from '../../../services/rfFlowFrameworkUpgradeService';
const dialogNames = {
  multiStepDownloadInstall: 'multiStepDownloadInstall',
  uninstall: 'uninstall',
};

export default {
  name: 'flow-framework-upgrade',
  components: { BackButton, RbxSwitch, GenericGrid, TooltipIconButton, RfPluginDownload, ConfirmDialog },
  data() {
    return {
      remount: 0,
      isUninstalling: false,
      isUpgradePersistent: true,
      currentDownloadState: null,
      downloadStateInterval: null,
      clearSelectedItems: null,
      service: rfFlowFrameworkUpgradeService,
      gridName: gridNames.RF_FLOW_FRAMEWORK_UPGRADE,
      toolbarTitle: 'Plugins',
      selectedItem: null,
      isDeletingDownloadState: false,
      appDownloadStates: [],
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.multiStepDownloadInstall]: false,
        [dialogNames.uninstall]: false,
      },
      remountDialog: {
        [dialogNames.multiStepDownloadInstall]: true,
      },
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Current Version',
          align: 'center',
          value: 'version',
          sortable: true,
        },
        {
          text: 'Installed',
          value: 'gridItemActions',
        },
      ],
    };
  },
  computed: {
    showCheckDownloadIcon() {
      return this.currentDownloadState === DOWNLOAD_STATES.DOWNLOAD_COMPLETE;
    },
    showDownloadProgressIcon() {
      return this.currentDownloadState === DOWNLOAD_STATES.DOWNLOADING;
    },
  },
  mounted() {
    this.fetchDownloadState();
  },
  destroyed() {
    this.downloadStateInterval = null;
  },
  methods: {
    installPlugins(plugins) {
      ;debugger
      const payload = plugins.map(selectedApp => ({ plugin: selectedApp.name }));

      return rfFlowFrameworkUpgradeService.install(payload).then(data => {
        this.remount++;
        return data;
      });
    },
    downloadPlugin(selectedApps) {
      const payload = selectedApps.map(selectedApp => ({ plugin: selectedApp.name }));
      return rfFlowFrameworkUpgradeService.download(payload).then(data => {
        this.startDownloadStateInterval();
        return data;
      });
    },
    deleteDownloadState() {
      this.isDeletingDownloadState = true;
      return rfFlowFrameworkUpgradeService
        .deleteDownloadState()
        .then(data => data)
        .finally(() => {
          this.fetchDownloadState();
          this.clearDownloadStateInterval();
          this.isDeletingDownloadState = false;
        });
    },
    fetchDownloadState() {
      rfFlowFrameworkUpgradeService.fetchDownloadState().then(data => {
        const { state: downloadState, plugins } = data;
        this.currentDownloadState = downloadState;
        switch (downloadState) {
          case DOWNLOAD_STATES.CLEARED:
            this.clearDownloadStateInterval();
            break;
          case DOWNLOAD_STATES.DOWNLOADING:
            this.startDownloadStateInterval();
            this.isUpgradePersistent = false;
            break;
          case DOWNLOAD_STATES.DOWNLOAD_COMPLETE:
            this.isUpgradePersistent = true;
            break;
        }
        if (Array.isArray(plugins)) {
          this.appDownloadStates = plugins;
        }
      });
    },
    onUninstall() {
      const payload = (this.selectedItem || []).map(selectedApp => ({ plugin: selectedApp.name }));
      if (!payload.length) {
        return;
      }
      this.isUninstalling = true;
      return rfFlowFrameworkUpgradeService
        .uninstall(payload)
        .then(data => {
          this.$toasted.show('Successfully uninstalled plugins.');
          this.closeDialog(this.dialogNames.uninstall);
          this.remount++;
          return data;
        })
        .catch(err => {
          this.$toasted.show('Failed to uninstall plugins.', { type: 'error' });
        })
        .finally(() => {
          this.isUninstalling = false;
        });
    },
    startDownloadStateInterval() {
      if (!this.downloadStateInterval) {
        this.downloadStateInterval = setInterval(() => {
          this.fetchDownloadState();
        }, REFRESH_TIME);
      }
    },
    clearDownloadStateInterval() {
      clearInterval(this.downloadStateInterval);
      this.downloadStateInterval = null;
    },
    closeDialog(dialogName, config) {
      this.dialog[dialogName] = false;
      if (config && config.unmount && this.remountDialog[dialogName]) {
        this.remountDialog[dialogName] = false;
        setTimeout(() => {
          this.remountDialog[dialogName] = true;
        }, 500);
      }
      this.selectedApp = {};
    },
    clearSelectedApps() {
      if (typeof this.clearSelectedItems === 'function') {
        this.clearSelectedItems();
      }
    },
    toggleDialog(dialogName, item, fn) {
      this.selectedItem = item;
      this.clearSelectedItems = fn;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    onGridItemInstallChange(item) {
      if (!item.is_installed) {
        this.toggleDialog(this.dialogNames.multiStepDownloadInstall, [{ ...item }]);
      } else if (item.is_installed) {
        this.toggleDialog(this.dialogNames.uninstall, [{ ...item }]);
      }
    },
  },
};
</script>
