<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ stepsModel[currentView].title }}</span>
    </v-card-title>
    <v-card-text>
      <v-stepper v-model="currentView">
        <v-stepper-header>
          <v-stepper-step :complete="currentView > 1" step="1"> 1 </v-stepper-step>
          <v-divider></v-divider>

          <v-stepper-step :complete="currentView > 2" step="2"> 2 </v-stepper-step>
          <v-divider></v-divider>

          <v-stepper-step :complete="currentView > 3" step="3"> 3 </v-stepper-step>
          <v-divider></v-divider>

          <v-stepper-step step="4"> 4 </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items class="stepper-container">
          <v-stepper-content step="1" class="pa-0">
            <v-list flat subheader dense class="text-left">
              <v-subheader>Selected Apps</v-subheader>

              <v-list-item v-for="(app, i) in apps" :key="`selected_apps-${i}`">
                <v-list-item-content>
                  <v-list-item-title v-text="app.displayName"></v-list-item-title>
                  <v-list-item-subtitle v-text="app.version"></v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-stepper-content>
          <v-stepper-content step="2" class="pa-0">
            <div class="text-left">
              <v-alert v-if="downloadStarted && currentDownloadState=='CLEARED'" class="mb-0" dense type="info">
                Fetching download status <strong>Please Wait!</strong>
              </v-alert>
              <v-alert v-if="currentDownloadState === 'DOWNLOADING'" class="mb-0" dense type="info">
                Downloading Selected App <strong>Please Wait!</strong>
              </v-alert>
              <v-alert class="mb-0" v-if="currentDownloadState === 'DOWNLOADED'" dense type="success">
                Downloading Completed
              </v-alert>
            </div>
          </v-stepper-content>
          <v-stepper-content step="3" class="pa-0">
            <v-list-item v-for="(app, i) in downloadedApps" :key="`selected_apps-${i}`">
              <v-list-item-content>
                <v-list-item-title v-text="app.pluginName"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-icon>
                <div v-if="app.isDownloading">
                  <span class="font-weight-medium">
                    <v-chip outlined color="downloadColor">Downloading</v-chip>
                  </span>
                </div>
                <div v-else-if="app.hasError">
                  <span class="font-weight-medium">
                    <v-chip outlined color="red">Failed</v-chip>
                  </span>
                </div>
                <div v-else-if="!app.isDownloading && !app.hasError">
                  <span class="font-weight-medium">
                    <v-chip outlined color="nubeGreen">Downloaded</v-chip>
                  </span>
                </div>
              </v-list-item-icon>
            </v-list-item>
          </v-stepper-content>
          <v-stepper-content step="4" class="pa-0">
            <v-list flat subheader dense class="text-left" v-if="currentDownloadState === 'DOWNLOADED'">
              <v-list-item-group v-if="successInstallApps.length > 0">
                <v-subheader>Installation Completed Apps</v-subheader>

                <v-list-item v-for="(successApps, i) in successInstallApps" :key="`install-complete-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="successApps.plugin"></v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <div>
                      <span class="font-weight-medium">
                        <v-chip outlined color="blue">Installed</v-chip>
                      </span>
                    </div>
                  </v-list-item-icon>
                </v-list-item>
              </v-list-item-group>
              <v-list-item-group v-if="failureInstallApps.length > 0">
                <v-subheader> Installation Failed Apps</v-subheader>
                <v-list-item v-for="(failedApps, i) in failureInstallApps" :key="`installation-failed-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="failedApps.plugin"></v-list-item-title>
                    <v-list-item-subtitle v-text="failedApps.version"></v-list-item-subtitle>
                    <v-list-item-subtitle v-text="failedApps.error"></v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <div>
                      <span class="font-weight-medium">
                        <v-chip outlined color="red">Failed</v-chip>
                      </span>
                    </div>
                  </v-list-item-icon>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
    <v-card-actions>
      <v-btn
        x-small
        class="btn-height-22 white--text"
        color="primary"
        @click="next"
        :loading="stepsModel[currentView].loader"
        :disabled="stepsModel[currentView].disabled"
        v-if="stepsModel[currentView].hasForward"
      >
        {{
          typeof stepsModel[currentView].buttonLabel === 'function'
            ? stepsModel[currentView].buttonLabel(serviceStatus)
            : stepsModel[currentView].buttonLabel
        }}
      </v-btn>
      <v-btn
        small
        color="pink"
        :disabled="stepsModel[currentView].disabled"
        v-if="stepsModel[currentView].hasCancelInstallation"
        @click="cancelInstallation"
        text
      >
        Cancel Install
      </v-btn>

      <v-btn small text v-if="stepsModel[currentView].hasCancel" @click="cancel"> Close </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import { DOWNLOAD_STATES } from './rfPluginDownloadStates.js';
export default {
  name: 'rf-plugin-download',
  props: [
    'selectedPlugins',
    'startDownload',
    'currentDownloadState',
    'appDownloadStates',
    'clearSelectedApps',
    'clearDownloadStateInterval',
    'deleteDownloadState',
    'closeDialog',
    'installPlugins',
  ],
  data() {
    return {
      currentView: 1,
      downloadStarted: false,
      successInstallApps: [],
      failureInstallApps: [],
      stepsModel: {
        1: {
          loader: false,
          back: 1,
          forward: 2,
          hasForward: true,
          disabled: false,
          hasCancel: true,
          title: 'Download',
          buttonLabel: 'Download',
        },
        2: {
          loader: false,
          back: 1,
          forward: 3,
          disabled: true,
          hasForward: true,
          hasCancel: true,
          title: 'Downloading',
          buttonLabel: 'Continue',
        },
        3: {
          loader: false,
          back: 1,
          forward: 3,
          disabled: false,
          hasForward: true,
          title: 'Install',
          buttonLabel: 'Install',
          hasCancelInstallation: true,
        },
        4: {
          loader: false,
          back: 4,
          forward: 4,
          disabled: false,
          hasForward: true,
          title: 'Complete',
          buttonLabel: 'Close',
        },
      },
    };
  },
  updated() {
    if(this.currentDownloadState === DOWNLOAD_STATES.DOWNLOAD_COMPLETE) {
      this.stepsModel[2].disabled = false;
    } else {
      this.stepsModel[2].disabled = true;
    }
  },
  watch: {
    currentDownloadState: function (newVal, oldVal) {
      this.showDownloadStateView(newVal);
    },
  },
  mounted() {
    this.showDownloadStateView(this.currentDownloadState);
  },
  computed: {
    apps() {
      return this.selectedPlugins.map(app => ({
        pluginName: app.name,
        version: app.version,
        displayName: app.name,
        state: app.state,
      }));
    },
    downloadedApps() {
      return (this.appDownloadStates || [])
        .filter(val => val && val.download)
        .map(downloadState => ({
          ...downloadState,
          name: downloadState.plugin,
          isDownloading: !downloadState.download,
          pluginName: downloadState.plugin,
          hasError: !!downloadState.error,
          errorMessage: downloadState.error,
        }));
    },
  },
  methods: {
    async next(step) {
      const currentView = this.currentView;
      switch (currentView) {
        case 1:
          this.download();
          break;
        case 2:
          this.currentView++;
          break;
        case 3:
          this.install();
          break;
        case 4:
          this.resetPluginInstallation();
          break;
        default:
          this.currentView++;
          break;
      }
    },
    install() {
      this.spinLoader(true);
      ;debugger
      return this.installPlugins(this.downloadedApps)
        .then(data => {
          this.successInstallApps = data.filter(val => val.installation);
          this.failureInstallApps = data.filter(val => !val.installation);
          this.currentView = 4;
        })
        .catch(err => {
          this.deleteDownloadState();
          this.$toasted.show('Unexpected error occured while bulk installing apps. Please try again');
        })
        .finally(() => {
          this.spinLoader(false);
        });
    },
    showDownloadStateView(currentDownloadState) {
      switch (currentDownloadState) {
        case DOWNLOAD_STATES.CLEARED:
          this.currentView = 1;
          this.stepsModel[2].disabled = true;
          break;
        case DOWNLOAD_STATES.DOWNLOADING:
          this.stepsModel[2].disabled = true;
          this.currentView = 2; // Download Waiting screen
          break;
        case DOWNLOAD_STATES.DOWNLOAD_COMPLETE:
          this.stepsModel[2].disabled = false;
          this.currentView = 2; // Show the install page
          this.clearDownloadStateInterval();
          break;
      }
    },
    back() {
      this.currentView--;
    },
    cancel() {
      if (this.currentView == 1) {
        this.deleteDownloadState();
      }
      this.closeDialog(true);
    },
    spinLoader(value) {
      this.stepsModel[this.currentView].loader = value;
    },
    download() {
      this.spinLoader(true);
      this.startDownload(this.selectedPlugins)
        .then(data => {
          if (data && data.message.toLowerCase() == 'download started') {
            this.downloadStarted = true;
          }
          this.stepsModel[2].disabled = true;
          this.spinLoader(false);
          this.currentView++;
        })
        .catch(() => {
          this.spinLoader(false);
        });
    },
    cancelInstallation() {
      this.resetPluginInstallation();
    },
    resetPluginInstallation() {
      this.currentView = 1;
      this.successInstallApps = [];
      this.failureInstallApps = [];
      this.stepsModel = {
        1: {
          loader: false,
          back: 1,
          forward: 2,
          hasForward: true,
          disabled: false,
          title: 'Download',
          buttonLabel: 'Download',
        },
        2: {
          loader: false,
          back: 1,
          forward: 3,
          disabled: false,
          hasForward: true,
          title: 'Downloading',
          buttonLabel: null,
        },
        3: {
          loader: false,
          back: 1,
          forward: 3,
          disabled: false,
          hasForward: true,
          title: 'Install',
          buttonLabel: 'Install',
          hasCancelInstallation: true,
        },
        4: {
          loader: false,
          back: 4,
          forward: 4,
          disabled: false,
          hasForward: false,
          title: 'Complete',
          buttonLabel: 'Close',
        },
      };
      this.deleteDownloadState();
      this.clearSelectedApps();
      this.closeDialog(true, { unmount: true });
    },
  },
};
</script>
<style scoped>
.stepper-container {
  max-height: 35vh;
  overflow-y: scroll;
}

.stepper-container::-webkit-scrollbar {
  width: 10px;
}

.stepper-container::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.stepper-container::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}

.stepper-container::-webkit-scrollbar-thumb:hover {
  background: white;
}
</style>
