<template>
  <span>
    <tooltip-icon-button
      v-if="!_value"
      :btnColor="disableClr"
      buttonSize="medium"
      btnIcon="mdi-toggle-switch-off-outline"
      :btnTooltip="disableTxt"
      :onClick="() => onInput(true)"
    />
    <tooltip-icon-button
      v-if="_value"
      :btnColor="enableClr"
      buttonSize="medium"
      btnIcon="mdi-toggle-switch-outline"
      :btnTooltip="enableTxt"
      :onClick="() => onInput(false)"
    />
  </span>
</template>
<script>
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';

export default {
  name: 'switch',
  props: ['value', 'enableText', 'disableText', 'enableColor', 'disableColor', 'disabled'],
  data() {
    return {};
  },
  components: { TooltipIconButton },
  computed: {
    _value: {
      get() {
        return this.value;
      },
      set(value) {
        return value;
      },
    },
    enableTxt() {
      return this.enableText ? this.enableText : 'Enable';
    },
    disableTxt() {
      return this.disableText ? this.disableText : 'Disable';
    },
    enableClr() {
      return this.enableColor ? this.enableColor : 'green';
    },
    disableClr() {
      return this.disableColor ? this.disableColor : 'orange';
    },
  },
  methods: {
    onInput(v) {
      if (this.disabled) {
        return;
      }
      this.$emit('input', v);
    },
  },
};
</script>
